import Link from "next/link";
import { sanityClient } from "@/lib/sanity";
import { PortableText } from "@portabletext/react";
import Footer from "@/components/footer";
import BlogHeader from "@/components/BlogHeader";
import BlogPostClient from "./BlogPostClient";

export const dynamicParams = false;

export async function generateStaticParams() {
  const slugs = await sanityClient.fetch(`
    *[_type=="post" && defined(slug.current)]{
      "slug": slug.current
    }
  `);

  return slugs.map((item) => ({
    slug: item.slug,
  }));
}

export default async function BlogPostPage({ params }) {
  const slug = params?.slug;

  // ðŸ”‘ DEV MODE SAFETY (prevents your error)
  if (!slug) {
    returnd (
      <>
        <BlogHeader />
        <main className="max-w-4xl mx-auto px-5 py-24 text-center">
          <p className="text-gray-500">Loading articleâ€¦</p>
        </main>
        <Footer />
      </>
    );
  }

  const post = await sanityClient.fetch(
    `*[_type=="post" && slug.current==$slug][0]{
      _id,
      title,
      excerpt,
      publishedAt,
      mainImage{ asset->{url} },
      categories[]->{_id,title},
      author->{ name, image{asset->{url}} },
      content
    }`,
    { slug }
  );

  if (!post) { 
    return (
      <>
        <BlogHeader />
        <main className="max-w-4xl mx-auto px-5 py-24 text-center">
          <h1 className="text-2xl font-bold">Post not found</h1>
        </main>
        <Footer />
      </>
    );
  };

  let relatedPosts = [];

  if (post.categories?.length) {
    const categoryIds = post.categories.map((c) => c._id);

    relatedPosts = await sanityClient.fetch(
      `*[_type=="post" &&
        references($categoryIds) &&
        slug.current != $slug][0...5]{
          _id,
          title,
          excerpt,
          "slug": slug.current,
          mainImage{ asset->{url} },
          categories[]->{title},
          author->{ name, image{asset->{url}} }
        }`,
      { categoryIds, slug }
    );
  }

  return (
    <>
      <BlogHeader />
      <BlogPostClient post={post} relatedPosts={relatedPosts} />
      <Footer />
    </>
  );
}